import React, { Component } from 'react'
import {Link, withRouter} from 'react-router-dom'
import './Equipment.css'
import axios from 'axios';
 import authService from '../../services/auth-service';
 import axiosService from '../../services/axios-service';
 import { API_URL } from '../../services/url';
export class Equipment extends Component {
    constructor(props) {
        super(props)
        this.state = {
          name : "",
          category : "",
          subcategory : "",
          company : "",
          equipmentStatus : "true",
          location :"",
          pinCode : "",
          userdata  :[],
          subcatdataonclick : []
        }
        // this.submit = this.submit.bind(this)
    }
    componentDidMount() {
      this.handleclick();
  }
  handleclick() {        
   axios.get(`${API_URL}/category/list`,axios.defaults.headers.common['w_auth'] = authService.getToken())
   .then(response => {
       console.log(response)
       let catitems = response.data;
       console.log(catitems)
           console.log(response.data);
           const data = response.data;
           this.setState({ userdata : data })
           console.log('userdata')
           console.log(this.state.userdata)
           }) .catch(error => {
           console.log(error.response); 
           })

 }
 siteclick = e =>  
 {
   e.preventDefault();
   console.log(this.state.category);
   // console.log(this.state.value);
   const sitemeet = this.state.category;
   console.log('sitemeet id')
   console.log(sitemeet)
   axios.get(`${API_URL}/subcategory/byCategory?categoryId=${sitemeet}`,axios.defaults.headers.common['w_auth'] = authService.getToken())
   .then(response => {
    //  console.log(`${API_URL}subcategory/byCategory?categoryId=${sitemeet}`)
     console.log(response)
    //  let catitems = response.data;
    //  console.log(catitems)
    //      console.log(response.data);
         const data = response.data;
         this.setState({ subcatdataonclick : data })
         console.log('subcatdataonclick')
         console.log(this.state.subcatdataonclick)
         }) .catch(error => {
         console.log(error.response); 
         })
//     fetch('list/siteoncompany?company_id='+sitemeet,{
//      method : 'GET',
//      headers : {
//        'Authorization': 'Bearer ' + this.state.token,
//        "Content-Type" : "application/json"
//    }
//    })
//    .then((res) => {
//      res.json().then((resp) => { 
//        console.log(resp.sitedata)
//        this.setState({ sitelistdata:resp.sitedata})
//        // this.parseJSON(this.state)
//      })
//    }
//  )

 }
    change = e => {
        this.setState ({
            [e.target.name] : e.target.value,
            company : localStorage.getItem('company_id')
        })
    }
        onSubmit = e => {
            e.preventDefault();
            console.log(this.state);
            console.log('hi')
            axios.post(`${API_URL}/equipment/save`, this.state,axios.defaults.headers.common['w_auth'] = authService.getToken())
        .then(function(response){ 
           console.log(response);
           console.log(response.status)
           if(response.status === 200)
           {
             alert('Equipment Has Been Addedd.')
             this.props.history.push('/ViewEquipment')
           }
        }.bind(this))
        .catch(error => {
            console.log(error.response);
          })

        }

  render() {
    return (
        <React.Fragment>
      <div>
        <main className="main">
            <div className="container-fluid">
                <div className="animated fadeIn">
                    <div className="row">
                       <div className="col-sm-12">
                       <div className="card custom-equipment-card">
                  <div className="card-header">
                    <strong>Equipment </strong>
                    <small>&nbsp;&nbsp;Form</small>
                  </div>
                  <div className="card-body">
                    <form autoComplete = "OFF">
                    <div className="row">
                      <div className="col-sm-12">
                        <div className="form-group">
                          <label for="name">Enter Equipment Name</label>
                          <input type="hidden" value={this.state.company} name="company" />
                          <input className="form-control" id="name" type="text" placeholder="Enter Equipment name" name="name" value = {this.state.name} onChange = {e => this.change(e)}/>
                        </div>
                      </div>
                    </div>
                   
                    <div className="row">
                      <div className="col-sm-6">
                        <div className="form-group">
                          <label for="ccnumber">Enter Category</label>
                          <select className="form-control" name="category" value={this.state.category}  onClick={e => this.siteclick(e)} onChange={(e) => this.setState({category: e.target.value})} >
    <option>Choose ....</option>
    {
              this.state.userdata ?
              this.state.userdata.map(function(item, _id) {
                return(
                    
        <option key ={_id} value={item._id}>{item.name}</option>
      )
              }
      )
              :
              <span>Data is loading....</span>
            }

    </select>

                        </div>
                      </div>
                      <div className="col-sm-6">
                        <div className="form-group">
                          <label for="ccnumber">Enter Sub Category</label>
                          <select className="form-control" name="subcategory" value={this.state.subcategory}  onChange={(e) => this.setState({subcategory: e.target.value})}>
            <option>chose....</option>
    {
              this.state.subcatdataonclick ?
              this.state.subcatdataonclick.map(function(item, id) {
                return(
                    
        <option key ={id} value={item.id}>{item.name}</option>
      )
              }
      )
              :
              <span>Data is loading....</span>
            }

    </select>
                        </div>
                      </div>
                    </div>
                   
                    <div className="row">
                      <div className="col-sm-6">
                        <div className="form-group">
                          <label for="name">Enter Location</label>
                          <input className="form-control" id="name" type="text" placeholder="Enter Location" name="location" value = {this.state.location} onChange = {e => this.change(e)}/>
                        </div>
                      </div>

                      <div className="col-sm-6">
                        <div className="form-group">
                          <label for="name">Enter Pincode</label>
                          <input className="form-control" id="name" type="number" placeholder="Enter Pincode" name="pinCode" value = {this.state.pinCode} onChange = {e => this.change(e)}/>
                        <small>Enter Number Only</small>
                        </div>
                      </div>
                    </div>
                   

                       {/* <div className="row">
                      <div className="col-sm-6">
                        <div className="form-group">
                          <label for="ccnumber">Enter Status</label>
                          <select className="form-control" name="equipmentStatus" value={this.state.equipmentStatus} onChange = {e => this.change(e)}>
                            <option defaultValue>Choose..</option>
                            <option value="final">Final</option>
                          </select>
                        </div>
                      </div>
                      <div className="col-sm-6">
                        <div className="form-group">
                          <label for="ccnumber">Enter Image</label>
                        <input type="file" className="form-control" value={this.state.image} name="image" onChange = {e => this.change(e)} />
                        </div>
                      </div>
                      </div> */}
                        <br />

                   <div className="row">
                        <div className="col-sm-12 text-center center-block">
                            <button type="button" className="btn btn-primary" onClick = {e => this.onSubmit(e)}>Submit</button>
                        </div>
                   </div>
                   </form>
                  </div>
                </div>   
                       </div>     
                    </div>
                </div>
            </div>
        </main>
      </div>
      </React.Fragment>
    )
  }
}
export default withRouter(Equipment)
