import React, { Component } from 'react';

import './App.css';
import Signin from './Components/Login/AddLogin';
import Register from './Components/Login/Register';
import {Route, Redirect} from 'react-router-dom';
import Header from './Components/Header/Header';
import Sidebar from './Components/Sidebar/Sidebar';
import Home from './Components/Home/Home';
import Body from './Components/Body/Body'
import { Category } from './Components/Category/Category';
import { ViewCategory } from './Components/Category/ViewCategory';
import { ViewSubCategory } from './Components/Category/ViewSubCategory';
import { SubCategory } from './Components/Category/SubCategory';
import { ViewEquipment } from './Components/Equipment/ViewEquipment';
import { Equipment } from './Components/Equipment/Equipment';
import { ViewCompany } from './Components/Company/ViewCompany';
import { Company } from './Components/Company/Company';
import { ViewRequirement } from './Components/Requirement/ViewRequirement';
import { Requirement } from './Components/Requirement/Requirement';
import { ViewManufacture } from './Components/Manufacture/ViewManufacture';
import { Manufacture } from './Components/Manufacture/Manufacture';
import { Subcat } from './Components/Category/Subcat';
import { ViewFeedback } from './Components/Feedback/ViewFeedback';
import { Enquiry } from './Components/Enquiry/Enquiry';

class App extends Component {
  constructor() {
    super();
    this.state = {
      route: 'signin',
      isSignedIn: false,
    }
  
  }


  onRouteChange = (route) => {
    if (route === 'signout') {
      this.setState({isSignedIn: false})
    } else if (route === 'home') {
      this.setState({isSignedIn: true})
    }
    this.setState({route: route});
  }
  render() {
    const { isSignedIn, route } = this.state;
    return (
      <div>
       
      {/* <div isSignedIn={isSignedIn} onRouteChange={this.onRouteChange} > */}
        <div isSignedIn={isSignedIn} onRouteChange={this.onRouteChange} />
        { localStorage.getItem('w_auth')
        ?
   
       <div className="app header-fixed sidebar-fixed aside-menu-fixed sidebar-lg-show">
         <Header/>
         <Sidebar />  
        
         <Route path={process.env.PUBLIC_URL + "/"} exact={true} component = {Home} /> 
         <Route path = {process.env.PUBLIC_URL +"/Category"} exact = {true} component = {Category} />
         <Route path = {process.env.PUBLIC_URL +"/ViewCategory"} exact = {true} component = {ViewCategory} />
         <Route path = {process.env.PUBLIC_URL +"/ViewSubCategory"} exact = {true} component = {ViewSubCategory}/>
         <Route path = {process.env.PUBLIC_URL +"/SubCategory"} exact = {true} component = {SubCategory} />
         <Route path = {process.env.PUBLIC_URL +"/ViewEquipment"} exact = {true} component = {ViewEquipment}/>
         <Route path ={process.env.PUBLIC_URL + "/Equipment"} exact = {true} component = {Equipment} />
         <Route path = {process.env.PUBLIC_URL +"/ViewCompany"} exact = {true} component = {ViewCompany}/>
         <Route path ={process.env.PUBLIC_URL + "/Company"} exact = {true} component = {Company} />
         <Route path = {process.env.PUBLIC_URL +"/ViewRequirement"} exact = {true} component = {ViewRequirement}/>
         <Route path = {process.env.PUBLIC_URL +"/Requirement"} exact = {true} component = {Requirement} />
         <Route path = {process.env.PUBLIC_URL +"/ViewManufacture"} exact = {true} component = {ViewManufacture}/>
         <Route path = {process.env.PUBLIC_URL +"/Manufacture"} exact = {true} component = {Manufacture} />
            <Route exact path={process.env.PUBLIC_URL + '/Subcat/:id'} exact={true} component={Subcat} />
            <Route exact path={process.env.PUBLIC_URL +'/ViewFeedback'} exact = {true} component={ViewFeedback} />
            <Route exact path={process.env.PUBLIC_URL +'/ViewEnquiry'} exact = {true} component={Enquiry} />
         </div>
      : (
             route === 'signin'
             ? <Signin  onRouteChange={this.onRouteChange}/>
             : <Register onRouteChange={this.onRouteChange}/>
            )
        }
        {/* </div> */}
     
      </div>
    );
  }
}

export default App;
