import React, { Component } from 'react'
import './Company.css'
import {Link, withRouter} from 'react-router-dom'
import axios from 'axios';
import authService from '../../services/auth-service';
import axiosService from '../../services/axios-service';
import { API_URL } from '../../services/url';
export class Company extends Component {
    constructor(props) {
        super(props)
        this.state = {
            name : "",
            city : "",
            address:"",
            email:"",
            contactNumber:"",
          contactPerson: "",
            contact:"",
            insertedBy: localStorage.getItem('companyId')
        }
        this.submit = this.submit.bind(this)
    }
    change = e => {
        this.setState ({
            [e.target.name] : e.target.value
        })
  }

        submit = e => {
          e.preventDefault();
          
          axios.post(`${API_URL}/company/save`, {
            name : this.state.name,
            city : this.state.city,
            address:this.state.address,
            email:this.state.email,
            contactNumber:this.state.contactNumber,
            contactPerson: this.state.contactPerson,
            insertedBy:this.state.insertedBy
         },axios.defaults.headers.common['w_auth'] = authService.getToken())
        .then(function(response){ 
           console.log(response.status);
           if(response.status === 200){
             alert("Company Added");

             this.props.history.push('/ViewCompany')

           }
        }.bind(this))
        .catch(error => {
            console.log(error.response);
          })

        }

  render() {
    return (
        <React.Fragment>
      <div>
        <main className="main">
        <ol class="breadcrumb">
       <li class="breadcrumb-item"> <Link to ={process.env.PUBLIC_URL+ "/ViewCategory"}>Home</Link></li>
          <li class="breadcrumb-item">
            Add Company
          </li>
          </ol>
            <div className="container-fluid">
                <div className="animated fadeIn">
                    <div className="row">
                       <div className="col-sm-12">
                       <div className="card custom-company-card">
                  <div className="card-header">
                    <strong>Company </strong>
                    <small>&nbsp;&nbsp;Form</small>
                  </div>
                  <div className="card-body">
                    <div className="row">
                      <div className="col-sm-6">
                        <div className="form-group">
                          <label for="name">Enter Company Name</label>
                          <input className="form-control" id="name" type="text" name="name" value = {this.state.name} onChange = {e => this.change(e)}/>
                        </div>
                      </div>

                      <div className="col-sm-6">
                        <div className="form-group">
                        <label for="ccnumber">Enter City</label>
                          <input className="form-control" name="city" value = {this.state.city} onChange = {e => this.change(e)}/>
                        </div>
                      </div>
                    </div>
                   
                    <div className="row">
                      <div className="col-sm-4">
                        <div className="form-group">
                          <label for="ccnumber">Enter Email</label>
                          <input className="form-control" name="email" value = {this.state.email} onChange = {e => this.change(e)}/>
                        </div>
                      </div>
                      <div className="col-sm-4">
                        <div className="form-group">
                          <label for="ccnumber">Enter Contact Number</label>
                          <input className="form-control" name="contactNumber" value = {this.state.contactNumber} onChange = {e => this.change(e)}/>
                        </div>
                      </div>
                      <div className="col-sm-4">
                        <div className="form-group">
                          <label for="ccnumber">Enter Contact Person</label>
                          <input className="form-control" name="contactPerson" value = {this.state.contactPerson} onChange = {e => this.change(e)}/>
                        </div>
                      </div>
                    </div>

                    <div className="row">
                      <div className="col-sm-12">
                        <div className="form-group">
                          <label for="ccnumber">Enter Address</label>
                          <textarea className="form-control" name="address" value = {this.state.address} onChange = {e => this.change(e)}></textarea>
                        </div>
                      </div>
                    </div>

                    
                    
                   
                   
                   <div className="row">
                        <div className="col-sm-12 text-center center-block">
                            <button type="button" className="btn btn-primary" onClick = {this.submit}>Submit</button>
                        </div>
                   </div>
                  </div>
                </div>   
                       </div>     
                    </div>
                </div>
            </div>
        </main>
      </div>
      </React.Fragment>
    )
  }
}
export default withRouter(Company)

