import React, { Component } from 'react'
import './Category.css'
import {Link} from 'react-router-dom'
import axios from 'axios'
import authService from '../../services/auth-service';
import axiosService from '../../services/axios-service'
import { API_URL } from '../../services/url';
export class Subcat extends Component {
    constructor(props)
    {
        super(props)
        this.state = {
                ui_id : this.props.match.params.id,    
                userdata : []             
                
                
           }
        }
        componentWillMount ()
        {
          this.handleClick();
        }
        handleClick () {
          let Editid = this.props.match.params.id;
          //console.log(this.props.match.params.id)
          axios.get(`${API_URL}/subcategory/byCategory?categoryid=${Editid}`,axios.defaults.headers.common['authorization'] = 'Bearer ' +	authService.getToken())
          .then(response => {
            console.log(response)
            let catitems = response.data;
            console.log(catitems)
                console.log(response.data);
                const data = response.data;
                this.setState({ userdata : data })
                console.log('userdata')
                console.log(this.state.userdata)
                }) .catch(error => {
                console.log(error.response); 
                })

        }
  render() {
    return (
        <React.Fragment>
      <div> 
            <main className="main">
                <div className="container-fluid">
                    <div className="row">
                       
                        <div className="col-sm-12">
                        <div className="card custom-category-card-view">
                        <div className="row">
                            <div className="offset-lg-8 col-lg-4 offset-md-8 col-md-4">
                              {/* <Link to ="/SubCategory"> <button type="button" className="btn btn-info custom-add-btn"><i className="icon-plus"></i>&nbsp;&nbsp;Add Sub Category</button> </Link> */}
                            <br /><br/>
                        </div>
                            </div>
                  <div className="card-header table-bordered">
                    <i className="fa fa-align-justify"></i> Sub Categories</div>
                  <div className="card-body">
                  <table className="table table-responsive-sm table-bordered">
                  <thead className="thead-dark">
                    <tr>
                      {/* <th>Category Name</th> */}
                      <th>Sub Category Name</th>
                      {/* <th>Category Description</th> */}
                    </tr>
                  </thead>
                  <tbody>
                  {
                            this.state.userdata ?
                            this.state.userdata.map(function(item, _id) {
                              return(
                                  
                      <tr key = {_id}>
                        {/* <td>{item.sub_cat || "NO DATA"}</td> */}
                        <td>{item.name || "NO DATA"}</td>
                        {/* <td>{item.sub_cat_description || 'NO DATA'}</td> */}
                      </tr>
                    )
                            }, this
                    )
                            :
                            <span>Data is loading....</span>
                   }

                  </tbody>
                </table>
                    {/* <ul className="pagination">
                      <li className="page-item">
                        <a className="page-link" href="#">Prev</a>
                      </li>
                      <li className="page-item active">
                        <a className="page-link" href="#">1</a>
                      </li>
                      <li className="page-item">
                        <a className="page-link" href="#">2</a>
                      </li>
                      <li className="page-item">
                        <a className="page-link" href="#">3</a>
                      </li>
                      <li className="page-item">
                        <a className="page-link" href="#">4</a>
                      </li>
                      <li className="page-item">
                        <a className="page-link" href="#">Next</a>
                      </li>
                    </ul> */}
                  </div>
                </div>
                        </div>
                    </div>
                </div>
            </main>
      </div>
      </React.Fragment>
    )
  }
}

export default Subcat
