import React, { Component } from 'react'
import './Category.css'
import {Link, withRouter} from 'react-router-dom'
import axios from 'axios'
import authService from '../../services/auth-service';
import axiosService from '../../services/axios-service';
import { API_URL } from '../../services/url';

export class Category extends Component {
    constructor(props) {
        super(props)
        this.state = {
            name : ""
          }
       
    }
    change = e => {
        this.setState ({
            [e.target.name] : e.target.value
        })
    }
    onSubmit = e =>
    {
       
      e.preventDefault();
    
          axios.post(`${API_URL}/category/save`,this.state,axios.defaults.headers.common['w_auth'] = authService.getToken())
          .then(function(res){ 
              console.log(res);
              //console.log(authService)
              if(res.data.name === this.state.name){
                  alert("category has been added")
                  this.setState({
                     name : "" 
                     })
                  this.props.history.push('/ViewCategory')
              }else{
                  alert(res.data);
              }
           }.bind(this))
           .catch(error => {
             console.log(error.res)
               console.log(error);
              //  authService.saveToken(response.headers["x-auth-user-token"]);
              //  console.log('authservice')
              //  console.log(authService)
              //  console.log('authservice token value')
              //  console.log(authService.getToken())
              //  console.log(getToken)
              
             })
      
      
    }
  
  render() {
    return (
        <React.Fragment>
      <div>
        <main className="main">
        <ol class="breadcrumb">
       <li class="breadcrumb-item"> <Link to ={process.env.PUBLIC_URL+ "/ViewCategory"}>Home</Link></li>
          <li class="breadcrumb-item">
            Add Category
          </li>
          </ol>
            <div className="container-fluid">
                <div className="animated fadeIn">
                    <div className="row">
                       <div className="col-sm-12">
                       <div className="card custom-category-card">
                  <div className="card-header">
                    <strong>Category </strong>
                    <small>&nbsp;&nbsp;Form</small>
                  </div>
                  <div className="card-body">
                    <div className="row">
                      <div className="col-sm-12">
                        <div className="form-group">
                          <label for="name">Enter Category Name</label>
                          <input className="form-control" id="name" type="text" placeholder="Enter your name" name="name" value = {this.state.name} onChange = {e => this.change(e)}/>
                        </div>
                      </div>
                    </div>
                   
                   <div className="row">
                        <div className="col-sm-12 text-center center-block">
                            <button type="button" className="btn btn-primary" onClick = {e => this.onSubmit(e)}>Submit</button>
                        </div>
                   </div>
                  </div>
                </div>   
                       </div>     
                    </div>
                </div>
            </div>
        </main>
      </div>
      </React.Fragment>
    )
  }
}

export default withRouter(Category)
