import React, { Component } from 'react'
import './Category.css'
import {Link} from 'react-router-dom'
import axios from 'axios'
import authService from '../../services/auth-service';
import axiosService from '../../services/axios-service'
import { API_URL } from '../../services/url';
export class ViewSubCategory extends Component {
    constructor(props)
     {
         super(props)
         this.state = {
             userdata : []
         }
          this.edithandler = this.edithandler.bind(this);
          this.deletehandler = this.deletehandler.bind(this)   
     }
     componentDidMount() {
         this.handleclick();
     }
     handleclick() {        
      axios.get(`${API_URL}/subcategory/list`,axios.defaults.headers.common['w_auth'] = authService.getToken())
      .then(response => {
          console.log(response)
          let catitems = response.data;
          console.log(catitems)
              console.log(response.data);
              const data = response.data;
              this.setState({ userdata : data })
              console.log('userdata')
              console.log(this.state.userdata)
              }).catch(error => {
              console.log(error.response); 
              })


  }

  edithandler ()
  {

  }
 async deletehandler (item)
  {
    await this.setState({_id:item.id,
    });
   console.log(this.state._id);
   let item1 = {
     _id:item.id
   }
     console.log(item1);
  const response = await axios.delete(`${API_URL}/subcategory/delete`,{data:{_id:item.id}}, axios.defaults.headers.common['w_auth'] = authService.getToken() )
      console.log(response)
      if (response.data.n===1){
        alert("Subcategory has been deleted");
        //window.location.reload();
        this.handleclick();
      }
    
  }
    render() {
        return (
            <React.Fragment>
          <div> 
                <main className="main">
                <ol class="breadcrumb">
       <li class="breadcrumb-item"> <Link to ={process.env.PUBLIC_URL+ "/ViewCategory"}>Home</Link></li>
          <li class="breadcrumb-item">
            View Subcategory
          </li>
          </ol>
                    <div className="container-fluid">
                        <div className="row">
                           
                            <div className="col-sm-12">
                            <div className="card custom-category-card-view">
                            <div className="row">
                                <div className="offset-lg-8 col-lg-4 offset-md-8 col-md-4">
                                  <Link to = {process.env.PUBLIC_URL+"/SubCategory"}> <button type="button" className="btn btn-info custom-add-btn"><i className="icon-plus"></i>&nbsp;&nbsp;Add Sub Category</button> </Link>
                                <br /><br/>
                            </div>
                                </div>
                      {/* <div className="card-header table-bordered">
                        <i className="fa fa-align-justify"></i> Sub Categories</div> */}
                      <div className="card-body">
                      <table className="table table-responsive-sm table-bordered">
                      <thead className="thead-dark">
                        <tr>
                          {/* <th>Category Name</th> */}
                          <th>Sub Category Name</th>
                          <th>Action</th>
                          {/* <th>Category Description</th> */}
                        </tr>
                      </thead>
                      <tbody>
                      {
                                this.state.userdata.length ?
                                this.state.userdata.map(function(item, id) {
                                  return(
                                      
                          <tr key = {id}>
                            {/* <td>{item.sub_cat || "NO DATA"}</td> */}
                            <td>{item.name || "NO DATA"}</td>
                            <td>
                          {/* <button type="button" className="btn btn-warning custom-edit-btn custom-btn" onClick = {this.edithandler.bind(this)}><i className="icon-pencil icons"></i></button> */}
                          <button type="button" className="btn btn-danger custom-btn" onClick = {this.deletehandler.bind(this,item)}><i className="icon-trash icons"></i></button>
                          </td>
                            {/* <td>{item.sub_cat_description || 'NO DATA'}</td> */}
                          </tr>
                        )
                                }, this
                        )
                                :
                                <span>Data is loading....</span>
                       }

                      </tbody>
                    </table>
                        {/* <ul className="pagination">
                          <li className="page-item">
                            <a className="page-link" href="#">Prev</a>
                          </li>
                          <li className="page-item active">
                            <a className="page-link" href="#">1</a>
                          </li>
                          <li className="page-item">
                            <a className="page-link" href="#">2</a>
                          </li>
                          <li className="page-item">
                            <a className="page-link" href="#">3</a>
                          </li>
                          <li className="page-item">
                            <a className="page-link" href="#">4</a>
                          </li>
                          <li className="page-item">
                            <a className="page-link" href="#">Next</a>
                          </li>
                        </ul> */}
                      </div>
                    </div>
                            </div>
                        </div>
                    </div>
                </main>
          </div>
          </React.Fragment>
        )
      }
    }

export default ViewSubCategory
