import React, { Component } from "react";
import "./Enquiry.css";
import { Link } from "react-router-dom";
import axios from "axios";
import authService from "../../services/auth-service";
import axiosService from "../../services/axios-service";
import { API_URL } from "../../services/url";
export class Enquiry extends Component {
  constructor(props) {
    super(props);
    this.state = {
      // ui_product_employe_array: [{
      userdata: []
      //  }],
    };
  }
  componentDidMount() {
    this.handleclick();
  }
  handleclick() {
    
    axios
      .get(
        `${API_URL}/enquiry/list`,
        (axios.defaults.headers.common["w_auth"] = authService.getToken())
      )
      .then(response => {
        // console.log(response)
        // let catitems = response.data;
        // console.log(catitems)
        //     console.log(response.data);
        console.log(response);
        const data = response.data;
        this.setState({ userdata: data });
        console.log("userdata");
        console.log(this.state.userdata);
      })
      .catch(error => {
        console.log(error);
      });
  }

  render() {
    let i = 0;
    return (
      <React.Fragment>
        <div>
          <main className="main">
            <div className="container-fluid">
              <div className="row">
                <div className="col-sm-12">
                  <div className="card custom-equipment-card-view">
                    {/* <div className="row">
                           <div className="offset-lg-8 col-lg-4 offset-md-8 col-md-4">
                             <Link to ={process.env.PUBLIC_URL+"/Requirement"}> <button type="button" className="btn btn-info custom-add-btn"><i className="icon-plus"></i>&nbsp;&nbsp;Add Requirement</button> </Link>
                           <br /><br/>
                       </div>
                           </div> */}
                    <div className="card-header table-bordered">
                      <i className="fa fa-align-justify" /> Enquiry
                    </div>
                    <div className="card-body custom-card-padding">
                      <table className="table table-responsive-sm table-bordered">
                        <thead className="thead-dark">
                          <tr>
                            <th scope="col">Name</th>
                            <th scope="col">Email</th>
                            <th scope="col">Mobile</th>
                            <th scope="col">Address</th>
                            <th scope="col">Pincode</th>
                            <th scope="col">Description</th>
                            <th scope="col">Date</th>
                          </tr>
                        </thead>
                        <tbody>
                          {this.state.userdata.length ? (
                            this.state.userdata.map(function(item, _id) {
                              {/* let name = item.name
                                ? item.company.name
                                : "no data";
                              let number = item.company
                                ? item.company.contactNumber
                                : "no data"; */}
                              //console.log(item.company.name)
                              return (
                                <tr key={_id}>
                                  <td>{item.name || 'NO DATA'}</td>
                                  <td>{item.email || 'NO DATA'}</td>
                                  <td>{item.mobile || 'NO DATA'}</td>
                                  <td>{item.address || 'NO DATA'}</td>
                                  <td>{item.pinCode || 'NO DATA'}</td>
                                  <td>{item.description || 'NO DATA'}</td>
                                  <td>
                                    {new Date(item.createdAt)
                                      .toString()
                                      .split(" ")
                                      .slice(0, 4)
                                      .join(" ") || "NO DATA"}
                                  </td>
                                  {/* <td>{item.company.name || "NO DATA"}</td> */}
                                  {/* <td>{item.company.contactPerson || "NO DATA"}</td>
         <td>{item.company.city || "NO DATA"}</td>
         <td>{item.company.contactNumber || "NO DATA"}</td>
               <td>{item.company.address || "NO DATA"}</td>*/}
                                </tr>
                              );
                            }, this)
                          ) : (
                            <span>No Data Present....</span>
                          )}
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </main>
        </div>
      </React.Fragment>
    );
  }
}

export default Enquiry;
