import React, { Component } from 'react'
import './Category.css'
import {Link} from 'react-router-dom'
import axios from 'axios'
import authService from '../../services/auth-service';
import axiosService from '../../services/axios-service';
import { API_URL } from '../../services/url';
export class SubCategory extends Component {
  constructor(props) {
    super(props)
    this.state = {
      name : "",
      category : "",
      userdata: []
    }
   
}
componentDidMount() {
  this.handleclick();
}

handleclick ()
{
              console.log('website on load content component did mount')
              axios.get(`${API_URL}/category/list`,axios.defaults.headers.common['w_auth'] = authService.getToken())
              .then(response => {
                let catitems = response.data;
                console.log(catitems)
                    console.log(response.data);
                    const data = response.data;
                    this.setState({ userdata : data })
                    }) .catch(error => {
                    console.log(error);
                    }) 
     }

change = e => {
    this.setState ({
        [e.target.name] : e.target.value
    })
}
onSubmit = e =>
{
   
 
  e.preventDefault();
	
  axios.post(`${API_URL}/subcategory/save`,this.state,axios.defaults.headers.common['w_auth'] = authService.getToken())
  .then(function(res){ 
      console.log(res);
      // authService.saveToken(res.headers["x-auth-user-token"]);
      // console.log('authservice')
      console.log(authService)
      // axios.defaults.headers.common['Authorization'] = AUTH_TOKEN;
      if(res.data.name === this.state.name){
          alert("Sub Category has been added")
          this.setState({
              name : "",
              category : "",
          })
          this.props.history.push('/ViewSubCategory')
      }else{
          alert(res.data);
      }
   }.bind(this))
   .catch(error => {
       console.log(error);
      //  authService.saveToken(response.headers["x-auth-user-token"]);
       console.log('authservice')
       console.log(authService)
       console.log(authService.getToken())
      //  console.log(getToken)
       console.log('headres')
       console.log('auth_token')
     })

  
  
}

render() {
return (
    <React.Fragment>
  <div>
    <main className="main">
    <ol class="breadcrumb">
       <li class="breadcrumb-item"> <Link to ={process.env.PUBLIC_URL+ "/ViewCategory"}>Home</Link></li>
          <li class="breadcrumb-item">
            Add Subcategory
          </li>
          </ol>
        <div className="container-fluid">
            <div className="animated fadeIn">
                <div className="row">
                   <div className="col-sm-12">
                   <div className="card custom-category-card">
              <div className="card-header">
                <strong>Sub Category </strong>
                <small>&nbsp;&nbsp;Form</small>
              </div>
              <div className="card-body">
              <div className="row">
                  <div className="col-sm-12">
                    <div className="form-group">
                      <label for="name">Enter Category Name</label>
                      <select className="form-control" value = {this.state.category} name = "category" onChange = {e=>this.change(e)}>
                              <option>Choose...</option>
                                                {
                              this.state.userdata.length ?
                              this.state.userdata.map(function(item, _id) {
                                return(
                                    <option value={item._id} key={_id}>{item.name}</option>   
                                    )
                                }, this
                        )
                                :
                                <span>Data is loading....</span>
                              }
                    
                                           </select>

                    </div>
                  </div>
                </div>

                <div className="row">
                  <div className="col-sm-12">
                    <div className="form-group">
                      <label for="name">Enter Sub Category Name</label>
                      <input className="form-control" id="name" type="text" placeholder="Enter your name" name="name" value = {this.state.name} onChange = {e => this.change(e)}/>
                    </div>
                  </div>
                </div>
               
               
               <div className="row">
                    <div className="col-sm-12 text-center center-block">
                        <button type="button" className="btn btn-primary" onClick = {e => this.onSubmit(e)}>Submit</button>
                    </div>
               </div>
              </div>
            </div>   
                   </div>     
                </div>
            </div>
        </div>
    </main>
  </div>
  </React.Fragment>
)
}
}
export default SubCategory
